import React from "react";
import { Equation } from "react-equation";
import Exchange from "./Exchange";
import "./Notes.css";
import GeneralRise from "./GeneralRise";

export function Section1() {
  return (
    <section className="Notes-section">
      <p>
        These are some rough notes on Marx, presented with an aim to
        contextualise some of his equations.
      </p>
      <p>
        Marx's Capital is a critique of existing economic theories. He is
        influenced in particular by J.S. Mill, Smith &amp; Ricardo, and heavily
        critical of Malthus, Bentham &amp; others. His approach emphasises the
        need to take the system as a whole, rather than analysing only partial
        elements removed from context. Unlike his contemporaries, Marx puts a
        lot of work into establishing the history and development of the
        phenomena he analyses, going beyond their superfical, present-day form
        to investigate how they change. He investigates key characteristics of
        each historical mode of production in Europe, and explains how each
        transitioned into the next.
      </p>
      <p>
        Capital is not present throughout history, and economic activity is more
        than just a series of exchanges. Production and distribution are
        related. Private property determines distribution. Relations of
        production shape the legal &amp; cultural system. Unique to capitalism
        is the emergence of labour as a commodity. As a commodity, it has an
        apparent equality with respect to buyers and sellers. The buyers pay a
        given price without distinction as to the political character of the
        labour recieved. The same labour can be sold to one buyer or another for
        the same price. This "equality" is capitalist "freedom".
      </p>
      <p>
        Marx identifies the dual character of commodities - the separation of
        use value (an object's useful purpose) and exchange value (the
        proportion at which the commodity can be exchanged for another). Money
        develops out of the need for a universal equivalent, and in particular
        circumstances money forms part of Capital. All commodities are produced
        by human labour. Use values are qualitative, and can't be compared
        directly - the usefulness of a coat can't be measured in terms of how
        useful a chair is, and so use is an unsuitable basis for exchange value.
        Exchange values must quantitative and thus comparable - in a given time
        and place, coats and chairs may be exchanged for each other in a given
        proportion. This proportion is determined by the average quantity of
        labour necessary for the production of each commodity.
      </p>
      <p>
        The following visualisation is one example of why trade based on
        exchange value creates the need for a universal equivalent. Set the
        value (in terms of average necessary labour) necessary to produce a
        whole unit of each commodity, and see the numbers required for each
        commodity to be 'priced' in terms of the equivalent proportion of every
        other commodity for sale in a single list of equivalent prices, without
        any fractional quantities (since trading 1.5 bricks for 3.2 loaves of
        bread would be irrational). Rather than have every seller maintain a
        daily-updated list of the relative exchange value for their commodity in
        terms of every other commodity, a single commodity becomes the universal
        equivalent that every commodity can be listed in - precious metals are
        ideal for this because they are difficult enough to produce that their
        exchange values are high, and they possess the quality of being able to
        be divided and recombined in fractional quantities.
      </p>
      <Exchange />

      <p>
        The Marxist theory of value diverges from other economists. Value
        corresponds to the labour socially embedded in a commodity, every other
        consideration is secondary. Marx discusses 'useful labour', which is
        labour that produces particular use values - the production of things in
        order to use them. In contrast with 'abstract labour', which is labour
        that produces exchange values - the production of things in order to
        sell them. A commodity is anything that is to be exchanged - not used
        immediately by those involved in its production. Workers purchasing
        commodities from capitalists leads to the capitalists' economic power
        becoming greater than the economic power of the workers. A commodity
        producing society must:
      </p>

      <ol>
        <li>Specialise</li>
        <li>
          Seperate use value from exchange value (by producing for exchange
          &amp; fulfilling use values by acquiring commodities from other
          producers)
        </li>
        <li>
          Have an extensive, well developed market utilising money as a means of
          exchange
        </li>
      </ol>

      <p>
        The social relationship among producers is characterised as follows: the
        producer's livelihood depends on commodities, but the value is not
        controlled by producers but by the economic relations of the whole
        society expressed through the market. Thus relations between producers
        appear to be relations between things. This produces a false belief in
        value being created by exchange.
      </p>
      <p>
        The exchange of commodities can be viewed from two perspectives.{" "}
        <span className="Notes-formula">C→M→C</span> represents selling a
        commodity in order to buy another commodity.{" "}
        <span className="Notes-formula">M→C→M'</span> represents buying a
        commodity in order to sell it, transforming money into commodities to
        make more money. In the sphere of circulation,{" "}
        <span className="Notes-formula">M→C→M'</span> is the general formula for{" "}
        <span className="Notes-term">Capital</span>. The difference between M
        and M' is <span className="Notes-term">surplus value</span>. The
        extraction of surplus value is what drives capitalism. Capital is
        personified in the capitalist, who is compelled to obey the laws of
        capital. Acquiring use values is not the aim of a capitalist - the sole
        aim is profit. Profit making is a never ending process.
      </p>
      <p>
        The market does not determine value. In particular, the market is not
        the source of value. The relations between all buyers and sellers,
        represented in the market, is a window through which we can view the
        totality of the relations of a given society that together determine the
        socially-average quantity of labour necessary for the production of a
        commodity. It is these relations, and the socially-average necessary
        quantity of labour in particular, that determine a commodity's value.
        Labour is the source of all value.
      </p>
      <p>
        The circulation of capital follows three stages. In the first stage,{" "}
        <span className="Notes-formula">M→C</span>, the capitalist acquires
        commodities in the market - a{" "}
        <span className="Notes-term">'circulation act'</span>. In the second
        stage, <span className="Notes-formula">P</span>, the purchased
        commodities are consumed productively, resulting in a commodity of more
        value than the commodity inputs. In the third stage,{" "}
        <span className="Notes-formula">C→M</span>, the capitalist returns to
        the market and transforms the commodity back into money in another
        circulation act.
      </p>
      <p>
        Marx's formula for the whole circuit is{" "}
        <span className="Notes-formula">M→C…P…C'→M'</span>. The ellipsis
        indicates that the process of circulation is interrupted, and{" "}
        <span className="Notes-formula">C' &amp; M'</span> designate{" "}
        <span className="Notes-formula">C &amp; M</span> increased by{" "}
        <span className="Notes-term">surplus-value</span>.{" "}
      </p>
      <p>
        Labour is found as a commodity on the market when the possessors of
        labour power put it up for sale. Labour is sold when the sellers have
        nothing else left to sell, as owning no means of production or
        subsistance leaves the labourer without a way to exchange commodities
        for necessary goods, nor to survive or reproduce alone. In this
        unnatural condition, the labourer has two choices - starve, or sell
        labour power. Like any commodity, the value of labour power (wages) is
        determined by the value needed to produce it. For the continuous
        reproduction of the labourer, this is the value necessary for
        subsistance, such as food, water, shelter and payment of debts. For
        labour that requires some degree of education, the value of subsistance
        incorporates the value of the education. Unique to labour power as a
        commodity, unlike any other commodity, is that its employment can add
        more value than it takes to reproduce - this is why it is termed{" "}
        <span className="Notes-term"> variable capital</span>. In contrast, all
        other commodities involved in the production process — the{" "}
        <span className="Notes-term">means of production</span> — are termed{" "}
        <span className="Notes-term">constant capital</span>.{" "}
      </p>
      <p>
        For example, imagine in a particular job it takes 4 hours of labour to
        produce the value equivalent to the labourers's daily reproduction. This
        is <span className="Notes-term">necessary labour</span>. Any additional
        labour beyond this is <span className="Notes-term">surplus labour</span>
        . Variable capital is also termed{" "}
        <span className="Notes-term">living labour</span>, because in the
        production process this labour is actively present, whereas constant
        capital is referred to as{" "}
        <span className="Notes-term">dead labour</span> because its value comes
        from the historic labour already embodied in it.
      </p>
      <p>
        Production is composed of constant capital (means of production),
        variable capital (waged or necessary labour) and surplus value (from
        surplus labour):{" "}
      </p>
      <Equation value="P = c+v+s" />
      <a name="surplus" href="#surplus"><h3>Rate of Surplus</h3></a>
      <Equation value="s/v = (surplus value) / (variable capital) = (surplus labour) / (necessary labour) = (surplus value) / (value of labour power)" />
    </section>
  );
}

export function Section2() {
  return (
    <section className="Notes-section">
      <p>
        Profit, interest and rent, the three forms of the non-wage income, are
        the divisions of surplus value among the ruling class. Interest &amp;
        rent are parasitic forms of income, as they don't involve the creation
        of surplus value through surplus labour. In contrast to the formula for
        profit through production,{" "}
        <span className="Notes-formula">M→C…P…C'→M'</span>, the formula for
        profit through interest or rent is simply{" "}
        <span className="Notes-formula">M→M'</span>.
      </p>
      <p>
        The mode of production of a given society changes as the social
        relations of production change. The historical mode of production of
        labour interdependency is known as communalism. Mediation of productive
        relations through the market is a component of the capitalist mode of
        production. Production is oriented around abstract labour rather than
        useful labour, because capital only needs work to be done in a manner
        optimal for extracting surplus, it does not optimise for usefulness.
      </p>
      <p>
        Capitalism similarly redefines the reproduction process, prioritising
        the production of new workers above the health of people reproducing and
        their children. Procreation is thus not apolitical. Just as reproduction
        is inevitable, the social organisation of labour is an unavoidable
        historical trend.{" "}
      </p>
      <h2>Transformation Problem</h2>
      <p>
        The transformation problem is the challenge of converting the values of
        commodities into their prices in the market.
      </p>
      <p>
        We define the following terms.{" "}
        <span className="Notes-formula">w = value; L = labour</span>. We further
        specify:{" "}
      </p>
      <p className="Notes-formula">
        L<sub>l</sub> = living labour;
      </p>
      <p className="Notes-formula">
        L<sub>d</sub> = dead labour (means of production);
      </p>
      <p className="Notes-formula">
        L<sub>n</sub> = necessary labour;
      </p>
      <p className="Notes-formula">
        L<sub>s</sub> = surplus labour;
      </p>
      <p>
        The value of a commodity is the sum of the dead and living labour that
        goes into producing it:{" "}
      </p>
      <Equation value="w=L_d + L_l" />

      <p>Living labour is composed of necessary and surplus labour: </p>
      <Equation value="L_l = L_n + L_s" />

      <p>
        Substituting living labour for its necessary and surplus parts, this
        gives:{" "}
      </p>
      <Equation value="w = L_d + L_n + L_s" />

      <p>
        The surplus is determined by the socially averaged rate of profit
        (explored later). Capitalists add a percentage 'markup' to commodity
        prices above the costs (constant capital &amp; wages) to pay rents,
        debts, and to render a profit. But capitalists are incentivised cut
        their own profit margins in order to undercut competitors and expand
        their own market share, leading to a race to the bottom (and a greater
        share of labour being rendered 'necessary' vs surplus). This is why we
        analyse in the general case based on the socially average rate of
        profit.{" "}
      </p>
      <section>
        <h3>Step 1: General Formula for Production</h3>
        <Equation value="P = c + v + s" />
        <p>Where:</p>
        <p className="Notes-formula">
          c = constant capital = cost of commodities used in production;
        </p>
        <p className="Notes-formula">
          v = variable capital = cost of labour used in production = wages;
        </p>
        <p className="Notes-formula">
          s = profit markup (determined by rate of profit);
        </p>
        <p>We expand this as:</p>
        <Equation value="P_production = c_means + v_wages + s_surplus" />
      </section>

      <section>
        <h3>Step 2: Rearranging Rate of Profit</h3>
        <p>Recalling Rate of Profit given by:</p>
        <Equation value="r = s/(c+v)" />
        <p>Expanded:</p>
        <Equation value="r_profit = s_surplus/(c_means + v_wages)" />

        <p>
          We can rearrange this to give a formula for surplus in terms of the
          rate of profit, means and wages, by multiplying both sides of the
          equation by means + wages and simplifying:
        </p>
        <aside className="Equation-multiline">
          <div>
            <Equation value="r *(c+v) = (s/(c+v)) * (c+v)" />
          </div>
          <div>
            <Equation value="r*(c+v) = s * ((c+v)/(c+v))" />
          </div>
          <div>
            <Equation value="r*(c+v) = s " />
          </div>
          <div>
            <Equation value="s = r * (c+v)" />
          </div>
        </aside>

        <p>Expanded:</p>
        <Equation value="s_surplus = r_profit * (c_means+v_wages)" />
      </section>

      <section>
        <h3>Step 3: Substituting Surplus</h3>
        <p>
          Take the equation for production from Step 1, and substituting{" "}
          <span className="Notes-formula">
            s<sub>surplus</sub>
          </span>{" "}
          for the equation derived from rate of profit in Step 2:
        </p>
        <aside className="Equation-multiline">
          <div>
            <span>Step 1:</span>
            <Equation value="P = c + v + s" />
          </div>

          <div>
            <span>Step 2:</span>
            <Equation value="s = r * (c+v)" />
          </div>

          <div>
            <span>Step 3:</span>
            <Equation value="P = c + v + r(c+v)" />
          </div>
        </aside>

        <p>Expanded:</p>
        <Equation value="P_production = c_means + v_wages + (r_profit * (c_means + v_wages))" />
      </section>

      <section>
        <h3>Step 4: Simplifying</h3>

        <p>
          We can simplify the result of Step 3 so that each term only occurs
          once, based on the 'distributive property'. The distributive property
          is important to understand for most of the following steps, so it is
          stated here but should be explored more deeply if the concept is
          unfamiliar.
        </p>
        <aside className="Equation-multiline">
          <div>
            <span>Distributative Property:</span>
            <Equation value="a*(x+y) = ax + ay" />
          </div>
          <div>
            <span>Step 3:</span>
            <Equation value="P = c + v + r(c+v)" />
          </div>

          <div>
            <span>Step 4:</span>
            <Equation value="P = (1 + r)*(c + v)" />
          </div>
        </aside>

        <p>Expanded:</p>
        <Equation value="P_production = (1 + r_profit) * (c_means + v_wages)" />
      </section>
      <section>
        <h3>Organic Composition</h3>
        <p>
          The organic composition of capital is the quantity of means of
          production per worker: the ratio between constant capital and variable
          capital. The term organic composition is used because it measures the
          proportion of the constituent 'organs' of capitalist production, dead
          and living labour.
        </p>
        <Equation value="c/v = (constant capital) / (variable capital) = (value of means) / (value of labour power) = (dead labour) / (necessary labour)" />
      </section>
      <section>
        <h3>Rate of Surplus or Rate of Exploitation</h3>
        <Equation value="s/v = (surplus value) / (variable capital) = (surplus labour) / (necessary labour) = (surplus value) / (value of labour power)" />
        <p>
          The rate of surplus is also known as the rate of exploitation, because
          it measures precicely how much of a worker's labour power goes to the
          benefit of the capitalist relative to the payment of wages.
        </p>
      </section>
      <section>
        <h3>Step 5: [unfinished]</h3>
        <section className="Equation-multiline">
          <div>
            <span>Value Equation:</span>
            <Equation value="w = l_d + l_n + l_s" />
          </div>
          <div>
            <span>Production Equation:</span>
            <Equation value="P = c + v + r(c+v)" />
          </div>
          <GeneralRise />
        </section>
      </section>
    </section>
  );
}
