import React from "react";
import {
  VictoryTheme,
  VictoryTooltip,
  VictoryPie
} from "victory";
import { EquationEvaluate } from "react-equation";


import Parameter from "./Parameter";
import "./Plotting.css";


export default function Plotting() {
  const [constantCapital, setConstantCapital] = React.useState(6);
  const [variableCapital, setVariableCapital] = React.useState(6);
  const [workingDay, setWorkingDay] = React.useState(12);

  const data = [
    {x: "Constant Capital", y: constantCapital},
    {x: "Variable Capital", y: variableCapital},
    {x: "Surplus Value", y: (workingDay - variableCapital)}
  ];
  
  return (
    <div className="Plotting">
      <div className="parameter-list">
        <Parameter
          onChange={setConstantCapital}
          value={constantCapital}
          name="Plotting-c"
          min={0}
          max={1000}
          step={0.1}
          label={`Constant capital (${constantCapital} hours)`}
        />
        <Parameter
          onChange={setVariableCapital}
          value={variableCapital}
          name="Plotting-v"
          min={0}
          max={workingDay}
          step={0.1}
          label={`Variable capital (${variableCapital} hours)`}
        />

        <Parameter
          onChange={setWorkingDay}
          value={workingDay}
          name="Plotting-w"
          min={variableCapital}
          max={24}
          step={0.1}
          label={`Working day (${workingDay} hours)`}
        />
      </div>

      <div className="Plotting-results">
        <div className="Plotting-chart">
          
            <VictoryPie 
              theme={VictoryTheme.material}
              data={data}
              labelComponent={<VictoryTooltip />}
              labels={({ datum }: any) => datum.x}
              animate={{ duration: 1000 }}
            />
              
          
        </div>
        <div className="Plotting-equations">
          <h3>Rate of Surplus</h3>
          <EquationEvaluate
            value="s/v"
            variables={{
              s: {
                type: "number",
                value: workingDay - variableCapital,
              },
              v: {
                type: "number",
                value: variableCapital,
              },
            }}
          />
          <h3>Rate of Profit</h3>
          <EquationEvaluate
            value="s/(c+v)"
            variables={{
              c: {
                type: "number",
                value: constantCapital,
              },
              s: {
                type: "number",
                value: workingDay - variableCapital,
              },
              v: {
                type: "number",
                value: variableCapital,
              },
            }}
          />
        </div>
      </div>
    </div>
  );
}
