import React from "react";
import {
  VictoryChart,
  VictoryStack,
  VictoryGroup,
  VictoryBar,
} from "victory";
import Parameter from "./Parameter";
import "./Plotting.css";

type State = [number, Function];

export default function GeneralRise() {
  const rateConstant: number = 0.4;
  const [rateWages, setRateWages]: State = React.useState(0.3);
  const rateSurplus: number = 1 - (rateConstant + rateWages);

  const [quantityNecessaries, setQuantityNecessaries]: State = React.useState(
    200
  );
  const quantityLuxuries: number = 1000 - quantityNecessaries;

  const workingDay: number = 10; // hours
  const workers: number = 100;

  // P = c + v + s
  // v +s = 1000 (wd * w)
  // c = 1/3 (c + v + s)
  //
  // 3c = c + v + s
  // 2 c = v + s
  // 2 c = 1000
  // c = 1/2 * 1000
  const valueWages: number = workingDay * workers * rateWages;
  const valueSurplus: number = workingDay * workers * rateSurplus;
  const valueConstant: number = ((valueWages + valueSurplus) / 60) * 40;

  const demandNecessaries: number = valueWages;
  const demandLuxuries: number = valueSurplus;

  // P = c + v + s
  //         v + s = 1000
  // P = 0
  const valueNecessaries: number = quantityNecessaries;

  const getBarData = () => {
    return [1, 2, 3, 4, 5].map(() => {
      return [
        { x: 1, y: Math.random() },
        { x: 2, y: Math.random() },
        { x: 3, y: Math.random() },
      ];
    });
  };
  /*
        SCENARIO
            c + v + s = P 

            Independent Variable: Rate of Wages (v/s)
            
            Dependent Variable: Price of Commodities
        
        */

  return (
    <div className="">
      <p>
        This scenario assumes that constant capital accounts for 40% of capital
        advanced. Use the wages slider to set the proportion of the value added
        in the working day allocated to wages vs surplus, and observe how prices
        of necessaries and luxuries are affected. Then, adjust the balance of
        production of necessaries and luxuries to restore prices to their real
        values.
      </p>
      <div className="parameter-list">
        <Parameter
          onChange={setRateWages}
          value={rateWages}
          name="w"
          min={0}
          max={1}
          step={0.01}
          label={
            <>
              <p>Wages are {rateWages * 100}%</p>
              <p>Surplus is {(1 - rateWages) * 100}%</p>
            </>
          }
        />
        <Parameter
          onChange={setQuantityNecessaries}
          value={quantityNecessaries}
          name="l"
          min={1}
          max={999}
          step={1}
          label={`Proportion of Necessaries to Luxuries (${quantityNecessaries}:${quantityLuxuries})`}
        />
      </div>
      <div>
        <p></p>
      </div>

      <div className="">
        <div>
          <VictoryChart domainPadding={{ x: 50 }} width={400} height={400}>
            <VictoryGroup offset={20} style={{ data: { width: 15 } }}>
              <VictoryStack colorScale={"red"}>
                {getBarData().map((data, index) => {
                  return <VictoryBar key={index} data={data} />;
                })}
              </VictoryStack>
              <VictoryStack colorScale={"green"}>
                {getBarData().map((data, index) => {
                  return <VictoryBar key={index} data={data} />;
                })}
              </VictoryStack>
              <VictoryStack colorScale={"blue"}>
                {getBarData().map((data, index) => {
                  return <VictoryBar key={index} data={data} />;
                })}
              </VictoryStack>
            </VictoryGroup>
          </VictoryChart>
        </div>
      </div>
    </div>
  );
}
